/*------------------------------------*\
    $GRIDS
\*------------------------------------*/
/**
 * Fluid and nestable grid system, e.g.:
 *
   <div class=gw>

       <div class="g  one-third">
           <p>One third grid</p>
       </div>

       <div class="g  two-thirds">
           <p>Two thirds grid</p>
       </div>

       <div class="g  one-half">
           <p>One half grid</p>
       </div>

       <div class="g  one-quarter">
           <p>One quarter grid</p>
       </div>

       <div class="g  one-quarter">
           <p>One quarter grid</p>
       </div>

   </div>
 *
 * Demo: jsfiddle.net/inuitcss/CLYUC
 *
 */

 .grid {
  background: #DDD;
  max-width: 1200px;
}

/* clear fix */
.grid:after {
  content: '';
  display: block;
  clear: both;
}


/**
 * Grid wrapper
 *
 * Shorthand and verbose
 */
.gw,
.grid-wrapper{
    /**
     * Negative margin to negate the padding on the first grid child.
     */
    margin-left:-$base-spacing-unit;

    @include media-query(palm) {
      margin-left: -$half-spacing-unit;
    }
    /**
     * The following declarations allow us to use the `.gw` class on lists.
     */
    list-style:none;
    margin-bottom:0;
    @extend .cf;
}


/**
 * Reverse grid order
 *
   <div class="gw  gw--rev">

       <div class="g  one-third">
           <p>Appears on the right</p>
       </div>

       <div class="g  two-thirds">
           <p>Appears on the left</p>
       </div>

   </div>
 *
 */
.gw--rev > .g,
.grid-wrapper--rev > .grid{
    float:right;
}


    /**
     * Very infrequently occuring grid wrappers as children of grid wrappers.
     */
    .gw > .gw,
    .grid-wrapper > .grid-wrapper{
        margin-left:0;
    }


    /**
     * Grid
     *
     * Shorthand and verbose
     */
    .g,
    .grid{
        float: left;
        padding-left: $base-spacing-unit;
        @if $global-border-box == false{
            @include vendor(box-sizing, border-box);
        }
        @include media-query(palm) {
    	    padding-left: $half-spacing-unit;
    	  }
    }

    .grid--full {
      padding-left:0;
      margin-left: 0;
      .g {
        padding-left:0;
      }
    }

    .responsive-flip {
		    @include media-query(palm) {
	    	display: none;
	    	//float: right;
	    }
    }

    .mob-fix {
        img {
	   	 	@include media-query(palm) {
		    	//max-width: 140px;
		    }
	    }
    }
