.mf-pop .mf-pop__desc::before {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 60%;
	// background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 80%);
	// background: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 80%);
	content: '';
	opacity: 0;
	-webkit-transform: translate3d(0,30%,0);
	transform: translate3d(0,30%,0);
}

.mf-pop .mf-pop__logo {
	position: relative;
	margin: 0 auto;
	width: 180px;
	color: #484c61;
	-webkit-transition: -webkit-transform 0.35s, all 0.35s;
	transition: transform 0.35s, all 0.35s;
	-webkit-transform: translate3d(0,65%,0);
	transform: translate3d(0,65%,0);
}

.mf-pop .mf-pop__desc::before,
.mf-pop p {
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
}

.mf-pop p {
	position: absolute;
	bottom: 0;
	left: 0;
	padding: $half-spacing-unit;
	text-align: center;
	color: #fff !important;
	font-size: 20px !important;
	margin-bottom: 0 !important;
	width: 100%;
	opacity: 0;
	-webkit-transform: translate3d(0,10px,0);
	transform: translate3d(0,10px,0);
}

.mf-pop:hover .mf-pop__logo {
	width: 160px;
	// left: 81%;
	color: #fff;
	-webkit-transform: translate3d(0,20%,0) translate3d(0,0px,0);
	transform: translate3d(0,20%,0) translate3d(0,0px,0);
}

.category .mf-directory-articles .post-tile.mf-pop:hover .mf-pop__logo--sampled {
	width: 120px !important;
}

.mf-pop:hover .mf-pop__desc::before ,
.mf-pop:hover p {
	opacity: 1;
	-webkit-transform: translate3d(0,0,0);
	transform: translate3d(0,0,0);
}

