.discover {
	width: 100%;
	display: table;
	position: relative;
	text-align: center;
	img {
		display: block;
		width: 100%;
		margin-bottom: $half-spacing-unit;
		@include media-query(palm) {
			width: 100%;
			height: auto;
		}
		@include media-query(mob) {
			width: 100%;
			height: auto;
		}
	}
	&:hover {
		.discover-cta {
		}
	}
}

.discover-cta {
	display: table-cell;
	position: absolute;
	pointer-events: none;
  vertical-align: middle;
	text-align: center;
	text-transform: uppercase;
	line-height: 1.4 !important;
	top: 11%;
	left: 12%;
	width: 75%;
	padding: $half-spacing-unit 0;
	@include font-size($h4-size);
	font-family: $quote-face;
	font-weight: 700;
	color: #fff;
	cursor: pointer;
	-webkit-transition:all 150ms ease-in;
       -moz-transition:all 150ms ease-in;
         -o-transition:all 150ms ease-in;
            transition:all 150ms ease-in;
}

