.category .mf-directory-articles .post-tile,
.post-tile {
	overflow: hidden;
	@extend .cf;
	height: auto;
	background: #fff;
	margin-bottom: $base-spacing-unit;
	&:nth-of-type(3n) {
		margin-right: 0;
	}
	img {
		-webkit-transition: all 150ms ease-in;
			 -moz-transition: all 150ms ease-in;
				 -o-transition: all 150ms ease-in;
						transition: all 150ms ease-in;
	}
	a {
	display: block;
	height: auto;
	border-bottom: 0;
	-webkit-transition: opacity  150ms ease-in;
     -moz-transition: opacity  150ms ease-in;
       -o-transition: opacity  150ms ease-in;
          transition: opacity  150ms ease-in;
		&:hover {
			text-decoration: none;
		  h3 {
				text-decoration: underline;
	    }
	    span {
		    text-decoration: underline;
	    }
			img {
				transform: scale(1.1);
			}
		}
    &:focus {
			outline: 3px solid $brand-color;
		}
	}
	.intro {
		padding: 0 $half-spacing-unit $half-spacing-unit $half-spacing-unit;
	}
	span {
		display: block;
		font-family: $quote-face;
		font-style: italic;
		padding: 0 0 $half-spacing-unit 0;
		margin-bottom: $base-spacing-unit;
		color: #000;
		-webkit-transition:all 150ms ease-in;
           -moz-transition:all 150ms ease-in;
             -o-transition:all 150ms ease-in;
                transition:all 150ms ease-in;
	}
	h3 {
		position: relative;
		z-index: 100;
		background: #fff;
		padding: $half-spacing-unit $half-spacing-unit $half-spacing-unit $half-spacing-unit;
    line-height: 1.2 !important;
		@include font-size($h3-size);
		margin-bottom: $half-spacing-unit /4;
		color: #000;
		-webkit-transition:all 150ms ease-in;
       -moz-transition:all 150ms ease-in;
         -o-transition:all 150ms ease-in;
            transition:all 150ms ease-in;
		@include media-query(lap) {
			@include font-size($h4-size);
			margin-bottom: $half-spacing-unit /4;
		}
		@include media-query(palm) {
			@include font-size($h4-size);
			margin-bottom: $half-spacing-unit /4;
		}
	}
	time {
		padding: $half-spacing-unit /2;
	}
	img {
		position: relative;
		z-index: 50;
		display:block;
		width: 100% !important;
		height: auto !important;
	}
	h4 {
		padding: $half-spacing-unit /2;;
		text-transform: uppercase;
		font-weight: normal;
		border-top:1px solid $keylinegrey;
		border-bottom:1px solid $keylinegrey;
	}
	p {
		margin-bottom: $half-spacing-unit;
		line-height: 1.4 !important;
		color: #000;
		@include font-size(16px);
		&:hover {
			text-decoration: none;
		}
	}
	time {
		@include font-size($milli-size);
		padding: $base-spacing-unit;
	}
	.author {
		display: block;
		@include font-size($milli-size);
		padding: $half-spacing-unit $base-spacing-unit $base-spacing-unit $base-spacing-unit;
		text-align: center;
	}
	.category {
		display: none;
		position: absolute;
		top: 0;
		right: 3%;
		color: #fff;
		background-color: $brand-color;
		padding: $half-spacing-unit $half-spacing-unit;
	}
	.event-time {
		margin-bottom: $half-spacing-unit;
		padding: 0;
		color: lighten($darkgrey, 10%);
	}
}

