		-webkit-transition:all 150ms ease-in;
           -moz-transition:all 150ms ease-in;
             -o-transition:all 150ms ease-in;
                transition:all 150ms ease-in;
		@include media-query(palm) {
			padding-right: $base-spacing-unit *2;
		}
	}
	.feature-img {
		width: 165px;
		height: 165px;
		@include media-query(palm) {
			width: 100%;
			height: auto;
		}
		@include media-query(mob) {
			width: 100%;
			height: auto;
		}
	}
	img {
		width: 165px;
		height: 165px;
		@include media-query(palm) {
			width: 100%;
			height: auto;
		}
		@include media-query(mob) {
			width: 100%;
			height: auto;
		}
	}
	.featured {
		background-color: #fff;
		padding: $half-spacing-unit $half-spacing-unit;
		position: absolute;
		top: 0;
		right: 0%;
		color: $brand-color;
		font-style: normal;
	}
	span {
		display: block;
		font-family: $quote-face;
		font-style: italic;
		padding: 0 $half-spacing-unit/2 $half-spacing-unit $half-spacing-unit/2;
		margin-bottom: $base-spacing-unit;
		color: #000;
		-webkit-transition:all 150ms ease-in;
           -moz-transition:all 150ms ease-in;
             -o-transition:all 150ms ease-in;
                transition:all 150ms ease-in;
	}
}

.small-thumb {
	img {
		width: 60px;
		height: 60px;
	}
}

.related-post-col {
	margin-left: $base-spacing-unit;
}

.related-post {
	margin-bottom: $base-spacing-unit;
	article {
		position: relative;
		border-bottom: 1px solid $keylinegrey;
		margin-bottom: $base-spacing-unit;

		&:last-of-type {
			border-bottom: 0;
		}
		a {
			display: block;
			@extend .cf;
			overflow: hidden;
			padding: $base-spacing-unit 0 $base-spacing-unit 0;
			height: auto;
			-webkit-transition:all 150ms ease-in;
	           -moz-transition:all 150ms ease-in;
	             -o-transition:all 150ms ease-in;
	                transition:all 150ms ease-in;
			&:hover {
				text-decoration: none;
				// background: $lightgrey;
			}
			&:first-of-type {
				padding-top: 0;
			}
		}
	}

	.related-post__right {
		margin-left: 188px;
	}
	h3 {
		margin-bottom: $half-spacing-unit;
	}
	.note {
		margin-left: 0;
		padding: $half-spacing-unit;
		color: #9e9e9e;
		font-style: italic;
		text-align: center;
	}
	p {
		margin-bottom: 0;
		@include font-size(16px);
		line-height: 1.4 !important;
		color: #000;
	}
	@include media-query(palm) {
		img {
			width: 60px;
			height: 60px;
		}
	}
	.media__image {
		float: none;
		position: absolute;
		left: 0;
	}
}

.yarpp-related h3 {
	font-weight: 700 !important;
	padding: $half-spacing-unit $half-spacing-unit 0 $half-spacing-unit !important;
	line-height: 1.4 !important;
  font-size: 100% !important;
  text-transform: none;
  margin: 0;
}

.yarpp-related a {
	font-weight: normal !important;
}

.featured-articles {
	ul {
		list-style-type: none;
		margin-left: 0;
	}
	h2 {
	  margin-top: 10px;
	  line-height: .5em;
		a {
		 @include font-size($h6-size);

		 font-family: sans-serif;
		 font-style: normal;
		 font-weight: bold;
		}
	}
	a, p {
		@include font-size($milli-size);
	}
	a {
		font-family: $quote-face;
	}
	li {
		cursor: pointer;
		display: block;
		@extend .cf;
		overflow: hidden;
		padding: 0 $half-spacing-unit 0 $half-spacing-unit;
		height: auto;
		border-bottom: 1px solid $keylinegrey;
		-webkit-transition:all 150ms ease-in;
           -moz-transition:all 150ms ease-in;
             -o-transition:all 150ms ease-in;
                transition:all 150ms ease-in;
        &:last-of-type {
			border-bottom: 0;
		}
		&:hover {
			text-decoration: none;
			background: $lightgrey;
		}
	}
}

.related-sidebar {

	@media only screen and (max-width : 1024px) {
		margin-left: 12px;
	}
	@media only screen and (max-width : 480px) {
		margin-left: 0px;
	}
  .one-third {
    width: 100% !important;
		@media only screen and (max-width : 1024px) {
	    width: 33.333% !important;
	  }
		@media only screen and (max-width : 480px) {
	    width: 100% !important;
	  }
  }

}
