/*------------------------------------*\
    $Carousel
\*------------------------------------*/



.carousel-article {
	min-height: 360px;
	overflow: hidden;
	//background: lighten($lightgrey, 2%);
	  -webkit-transition:all 150ms ease-in;
       -moz-transition:all 150ms ease-in;
         -o-transition:all 150ms ease-in;
            transition:all 150ms ease-in;
    &:hover {
		//background: darken($lightgrey, 3%);
		text-decoration: none;
			h2 {
				color: $brand-color;
			}
			.title {
				padding: $half-spacing-unit $base-spacing-unit $half-spacing-unit $base-spacing-unit;
				border-top: 1px solid $brand-color;
				border-bottom: 1px solid $brand-color;
			}
			span {
				color: $brand-color;
			}
		}
	a {
		overflow: hidden;
		height: auto;
		&:hover {
			background: darken($lightgrey, 3%);
			text-decoration: none;
		}
	}
	.event-time-car {
		padding: 0;
		margin-bottom: 0;
		text-align: center;
		@include font-size($milli-size);
		color: lighten($darkgrey, 10%);
		font-style: italic;
	}
	span {
		display: block;
		font-family: $quote-face;
		font-style: italic;
		padding: 0 $base-spacing-unit $half-spacing-unit $base-spacing-unit;
		margin-bottom: $base-spacing-unit;
		color: #000;
		    -webkit-transition:all 150ms ease-in;
           -moz-transition:all 150ms ease-in;
             -o-transition:all 150ms ease-in;
                transition:all 150ms ease-in;
	}
	img {
		width: 100% !important;
		height: auto;
	}
	.carousel-image {
		max-height: 120px;
		overflow: hidden;
		margin-bottom: $half-spacing-unit;
		@include media-query(palm) {
			max-height: 70px;
		}
	}
	h2 {
		@include font-size($h4-size);
		color: #000;
		line-height: 1.4;
		text-align: center;
		margin-bottom: 0;
		    -webkit-transition:all 150ms ease-in;
           -moz-transition:all 150ms ease-in;
             -o-transition:all 150ms ease-in;
                transition:all 150ms ease-in;
	}
	.title {
		padding: $half-spacing-unit $base-spacing-unit $half-spacing-unit $base-spacing-unit;
		border-top: 1px solid $darkgrey;
		border-bottom: 1px solid $darkgrey;
	}
	p {
		padding: $half-spacing-unit $base-spacing-unit;
		color: #000;
	}
	.post-categories {
		a {
			display: inline;
			padding-bottom: 0;
			color: $brand-color;
			&:hover {
				color: #000;
				background: transparent;
			}
		}
	}
}

.carousel-article.category-northern-quarter {
	overflow: hidden;
	color: #fff;
	&:hover {
		text-decoration: none;
		h3 {
			color: lighten($brand-color, 20%);
		}
		.title {
			border-top: 0px;
			border-bottom: 1px solid lighten($brand-color, 20%);
		}
		span {
			color: lighten($brand-color, 20%);
		}
	}
	.title {
		background: darken($darkgrey, 5%);
		padding: $half-spacing-unit $base-spacing-unit $half-spacing-unit $base-spacing-unit;
		border-top: 0px;
		border-bottom: 1px solid #fff;
	}
	.intro {
		background: darken($darkgrey, 5%);
	}
	span {
		color: #fff;
	}
	h2 {
		color: #fff;
		margin-bottom: 0;
		text-align: center;
	}
	a {
		background: transparent !important;
		color: lighten($brand-color, 20%);
	}
	p {
		color: #fff;
		padding-top: $half-spacing-unit;
		background: darken($darkgrey, 5%);
	}
	img {
		margin-bottom: $half-spacing-unit;
	}
	.post-categories {
		&:before {
			color: lighten($brand-color, 20%);
		}
		a {
			color: #fff;
			&:hover {
				color: lighten($brand-color, 20%);
			}
		}
	}
}

