.owl-nav {
  @extend .cf;
  position: relative;
  top: -20px;
  margin: 0 auto;
  width: 200px;
}

.instagram-pics .owl-nav {
  top: 0px;
}

#owl-directory-videos {
  position: relative;
  .owl-stage-outer {
    height: 447px;
    overflow: hidden;
    @include media-query(palm) {
  		height: 447px;
  	}
    @include media-query(mob) {
  		height: 300px;
  	}
  }
  .owl-nav {
    position: absolute;
    z-index: 200;
    top: 44%;
    width: 100%;
  }
  .owl-prev,
  .owl-next {
   position: absolute;
   color: #fff;
   border: 0;
   border-radius: 50%;
   background: rgba(255,255,255,0.6);
  }
  .owl-prev {
   left: -25px;
   background-image: url(data:image/svg+xml;base64,);
   background-size: 30px;
   background-repeat: no-repeat;
   background-position: center;
   width: 50px;
   height: 50px;
   @include media-query(palm) {
 		left: 0px;
 	 }
  }
  .owl-next {
   right: -25px;
   background-image: url(data:image/svg+xml;base64,);
   background-size: 30px;
   background-repeat: no-repeat;
   background-position: center;
   width: 50px;
   height: 50px;
   @include media-query(palm) {
 		right: 0px;
 	 }
  }
}

.owl-prev {
  float: left;
}
.owl-next {
  float: right;
}

.owl-prev,
.owl-next {
  display: inline-block;
  @include mf-button();
}

.owl-carousel {
  // display: none;
  width: 100%;
  margin: $base-spacing-unit auto 0 auto;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1; }
  .owl-carousel .owl-stage {
    position: relative;
    -ms-touch-action: pan-Y;
    -moz-backface-visibility: hidden;
    margin-bottom: $base-spacing-unit;
    /* fix firefox animation glitch */ }
  .owl-carousel .owl-stage:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0; }
  .owl-carousel .owl-stage-outer {
    position: relative;
    overflow: hidden;
    /* fix for flashing background */
    -webkit-transform: translate3d(0px, 0px, 0px); }
  .owl-carousel .owl-wrapper,
  .owl-carousel .owl-item {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    -ms-backface-visibility: hidden;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0); }
  .owl-carousel .owl-item {
    position: relative;
    min-height: 1px;
    float: left;
    -webkit-backface-visibility: hidden;
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none; }
  .owl-carousel .owl-item img {
    display: block;
    width: 100%; }
  .owl-carousel .owl-nav.disabled,
  .owl-carousel .owl-dots.disabled {
    display: none; }
  .owl-carousel .owl-nav .owl-prev,
  .owl-carousel .owl-nav .owl-next,
  .owl-carousel .owl-dot {
    cursor: pointer;
    cursor: hand;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
  .owl-carousel.owl-loaded {
    display: block; }
  .owl-carousel.owl-loading {
    opacity: 0;
    display: block; }
  .owl-carousel.owl-hidden {
    opacity: 0; }
  .owl-carousel.owl-refresh .owl-item {
    visibility: hidden; }
  .owl-carousel.owl-drag .owl-item {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: center;}
  .owl-carousel.owl-grab {
    cursor: move;
    cursor: grab; }
  .owl-carousel.owl-rtl {
    direction: rtl; }
  .owl-carousel.owl-rtl .owl-item {
    float: right; }

/* No Js */
.no-js .owl-carousel {
  display: block; }

/*
 *  Owl Carousel - Animate Plugin
 */
.owl-carousel .animated {
  animation-duration: 1000ms;
  animation-fill-mode: both; }

.owl-carousel .owl-animated-in {
  z-index: 0; }

.owl-carousel .owl-animated-out {
  z-index: 1; }

.owl-carousel .fadeOut {
  animation-name: fadeOut; }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

/*
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  transition: height 500ms ease-in-out; }

/*
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease; }

.owl-carousel .owl-item img.owl-lazy {
  transform-style: preserve-3d; }

/*
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000; }

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  background: url("owl.video.play.png") no-repeat;
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: transform 100ms ease; }

.owl-carousel .owl-video-play-icon:hover {
  -ms-transform: scale(1.3, 1.3);
      transform: scale(1.3, 1.3); }

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none; }

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease; }

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
  height: 100%;
  width: 100%; }



.rsThumbs {
	background: transparent !important;
	color: #000 !important;
}
.galleryTextThumbs .rsTmb {
	padding: 8px 16px;
	-webkit-transition:all 100ms ease-in !important;
			 -moz-transition:all 100ms ease-in !important;
			 -o-transition:all 100ms ease-in !important;
					transition:all 100ms ease-in !important;
}

.galleryTextThumbs .rsThumb.rsNavSelected .rsTmb {
	padding: 8px 16px 8px 28px;
	-webkit-transition:all 100ms ease-in !important;
			 -moz-transition:all 100ms ease-in !important;
			 -o-transition:all 100ms ease-in !important;
					transition:all 100ms ease-in !important;
					@include media-query(palm) {
						padding: 8px;
					}
}

.galleryTextThumbs .rsThumbs .rsThumb {
	margin: 0 12px 12px 12px;
	border-bottom: 0 !important;
	background: #dddddd !important;
